import { Injectable } from '@angular/core';
import { SocketService } from './socketio/socket.service'
import { Subject } from 'rxjs/Subject';
declare var moment: any;
@Injectable()
export class DataService {

  public sensorData: any = {}
  isUpdateClick = false;
  focusedSensor: string = 'ec';
  isUpdateSuccess = false;
  image: string = "assets/img/Spinner.svg";
  sensorLog: any = {
    'ec': [],
    'ph': [],
    'o2': [],
    'co2': [],
    'temp': [],
    'humi': []
  }
  settingData: any = {
    'ec-setpoint': 0,
    'ec-working': 0,
    'ec-detecting': 0,
    'ec-mode': 2,
    'ect-working': 0,
    'ect-list': [],
    'timerMode': false,
    'sensorMode': false,
    'timerList': []
  }
 
  public sensorUpdate = new Subject<any>();
  public ecControlUpdate = new Subject<any>();
  public ecTimerControlUpdate = new Subject<any>();
  constructor(private io: SocketService) {
    this.io.socket.on('SENSOR_DATA', (data: any) => {
      this.sensorData = data;

      let dt = data.date + " " + data.time;
      let dateTime = moment(dt);

      this.sensorLog['ec'].push({
        x: dateTime,
        y: data.ec
      })
      if (this.sensorLog['ec'].length >= 60) this.sensorLog['ec'].shift();

      this.sensorLog['ph'].push({
        x: dateTime,
        y: data.ph
      })
      if (this.sensorLog['ph'].length >= 60) this.sensorLog['ph'].shift();

      this.sensorLog['co2'].push({
        x: dateTime,
        y: data.co2
      })
      if (this.sensorLog['co2'].length >= 60) this.sensorLog['co2'].shift();

      this.sensorLog['temp'].push({
        x: dateTime,
        y: data.temp
      })
      if (this.sensorLog['temp'].length >= 60) this.sensorLog['temp'].shift();

      this.sensorLog['o2'].push({
        x: dateTime,
        y: data.o2
      })
      if (this.sensorLog['o2'].length >= 60) this.sensorLog['o2'].shift();

      this.sensorLog['humi'].push({
        x: dateTime,
        y: data.humi
      })
      if (this.sensorLog['humi'].length >= 60) this.sensorLog['humi'].shift();

      this.sensorUpdate.next(data);
    });

    this.io.socket.on("SHORT_LOGGER_REP", (data: any) => {
      data.forEach(d => {

        let dt = d.date + " " + d.time;
        let dateTime = moment(dt);

        this.sensorLog['ec'].push({
          x: dateTime,
          y: d.ec
        })

        this.sensorLog['ph'].push({
          x: dateTime,
          y: d.ph
        })

        this.sensorLog['co2'].push({
          x: dateTime,
          y: d.co2
        })

        this.sensorLog['temp'].push({
          x: dateTime,
          y: d.temp
        })

        this.sensorLog['o2'].push({
          x: dateTime,
          y: d.o2
        })

        this.sensorLog['humi'].push({
          x: dateTime,
          y: d.humi
        })

      });
    });

    this.io.socket.on('REP_SETTING', (data)=>{
      
      this.settingData['ec-working'] = data['ec-working'];
      this.settingData['ec-setpoint'] = data['ec-setpoint'];
      this.settingData['ec-detecting'] = data['ec-detecting'];
      this.settingData['ec-mode'] = data['ec-mode'] 
      this.settingData['ect-working'] =  data['ect-working']
      this.settingData['ect-list'] = data['ect-list']
      this.settingData['ec-cal'] = data['ec-cal'];
      this.settingData['ph-cal'] = data['ph-cal'];
      // console.log('REP', this.settingData);
      if( this.settingData['ec-mode'] == 0){
        this.settingData['sensorMode'] = true;
        this.settingData['timerMode'] = false;
      }

      if( this.settingData['ec-mode'] == 1){
        this.settingData['sensorMode'] = false;
        this.settingData['timerMode'] = true;
      }
      
      this.refreshECTimerList( data['ect-list']);
      // console.log(this.settingData)
    })
    

    this.io.socket.on('EC_STATUS', (data)=>{
      this.ecControlUpdate.next(data);
    })

    this.io.socket.on('ECT_STATUS', (data)=>{
      this.ecTimerControlUpdate.next(data);
    })

    this.io.socket.on('SET_SETTING_SUCCESS', ()=>{
     
      setTimeout( ()=>{
         this.isUpdateSuccess = true;
      },2000);
    });

    this.io.socket.emit('REQ_SETTING');
    this.io.socket.emit('SHORT_LOGGER_REQ');
  }

  refreshECTimerList(list):void {
    // this.settingData.timerList.clear();
    this.settingData.timerList = []
      var timerlist = list;
      timerlist.forEach( timer=>{
        var hour =  Math.floor(timer / 60)
        var min = timer % 60

        var hstr = hour.toString();
        var mstr = min.toString();
        if(hour < 10){
          hstr = "0" + hstr;
        }
         if(min < 10){
          mstr = "0" + mstr;
        }
        var t = {
          'hour': hstr,
          'min': mstr
        }
        this.settingData.timerList.push(t);
      })
  }

  updateECTimeList(timer):void {
        var hour =  Math.floor(timer / 60)
        var min = timer % 60

        var hstr = hour.toString();
        var mstr = min.toString();
        if(hour < 10){
          hstr = "0" + hstr;
        }
         if(min < 10){
          mstr = "0" + mstr;
        }
        var t = {
          'hour': hstr,
          'min': mstr
        }
        this.settingData.timerList.push(t);
  }

}



