import { Component, OnInit } from '@angular/core';
import { DataService } from '../../../shared/data.service'
import { Subscription } from 'rxjs/Subscription'

import * as moment from 'moment'
@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  date: String;
  time: String;
  dtUpdate: Subscription;
  constructor(private dataService: DataService) {
    this.date = moment().format('MMM Do');
    this.time = moment().format('HH:mm:ss')
  }

  ngOnInit() {
    this.dtUpdate = this.dataService.sensorUpdate.asObservable().subscribe(data => {
      // console.log(data);
      var datetime = data.date + " " + data.time;
      this.date = moment(datetime).format('MMM Do');
      this.time = moment(datetime).format('HH:mm:ss')
    });
  }
  ngOnDestroy() {
    this.dtUpdate.unsubscribe();
  }

}



